import {createGlobalStyle} from "styled-components";

export const GlobalStyle = createGlobalStyle`
  *,
  *::before,
  *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    background-color: #2A2927;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
  }

  a {
    color: white;
    text-decoration: none;
  }
`