import React, {ChangeEvent} from 'react';
import {styled} from "styled-components";
import {FlexWrapper} from "../components/Wrapper";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faChevronLeft, faChevronRight} from "@fortawesome/free-solid-svg-icons";
import {incomeRateFilterType} from "../App";
import {Toggle} from "../components/Toggle";

type PropsType = {
    incomeRateFilterChange: (filter: incomeRateFilterType) => void
    incomeSum: number
    rateSum: number
    balance: number
    changeFirstDate: (newDate: string) => void
    changeSecondDate: (newDate: string) => void
    firstDate: string
    secondDate: string
    setThisMonth: () => void
    setLastMonth: () => void
    setThisYear: () => void
    setAllTime: () => void
}

export const Header = (props: PropsType) => {

    const incomeFilterChangeHandler = () => {
        props.incomeRateFilterChange('income')
    }

    const rateFilterChangeHandler = () => {
        props.incomeRateFilterChange('rate')
    }

    const allFilterChangeHandler = () => {
        props.incomeRateFilterChange('all')
    }

    const changeFirstDateHandler = (event: ChangeEvent<HTMLInputElement>) => {
        props.changeFirstDate(event.currentTarget.value)
    }

    const changeSecondDateHandler = (event: ChangeEvent<HTMLInputElement>) => {
        props.changeSecondDate(event.currentTarget.value)
    }

    return (
        <StyledHeader>
            <FlexWrapper direction={"column"}>
                <MenuBar>
                    <Toggle/>
                    <FontAwesomeIcon icon={faChevronRight} cursor={'pointer'} className="chevronRight"/>
                </MenuBar>
                <MonthBar>
                    <input type="date" id="firstDate" value={props.firstDate} onChange={changeFirstDateHandler}/>
                    <input type="date" id="secondDate" value={props.secondDate} onChange={changeSecondDateHandler}/>
                </MonthBar>
                <ButtonBar>
                    <button onClick={props.setThisMonth}>This month</button>
                    <button onClick={props.setLastMonth}>Last month</button>
                    <button onClick={props.setThisYear}>This year</button>
                    <button onClick={props.setAllTime}>All time</button>
                </ButtonBar>
                <RateBar>
                    <p>Income</p>
                    <p>{props.incomeSum} $</p>
                </RateBar>
                <RateBar>
                    <p>Expense</p>
                    <p>{props.rateSum} $</p>
                </RateBar>
                <RateBar>
                    <p>Balance</p>
                    <p>{props.balance} $</p>
                </RateBar>

            </FlexWrapper>
            <ButtonBar>
                <button onClick={allFilterChangeHandler}>All</button>
                <button onClick={incomeFilterChangeHandler}>Income</button>
                <button onClick={rateFilterChangeHandler}>Expense</button>
            </ButtonBar>
        </StyledHeader>
    )
}

const StyledHeader = styled.header`
  width: inherit;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;

  i {
    color: #BECCE0;
    cursor: pointer;
  }

  p {
    font-family: 'Gill Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
  }
`

export const MenuBar = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #BECCE0;
  padding: 5px 10px;
  
  .chevronRight{
    display: none;
  }
  
`

const MonthBar = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 8px 10px;
  color: #BECCE0;
  
  input[type="date"]{
    padding: 5px 5px;
    background-color: #1B2F4A;
    color: #BECCE0;
    border: none;
    font-family: 'Gill Sans', sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    transition: .1s ease-in-out;
  }
  
  ::-webkit-calendar-picker-indicator{
    background-color: #BECCE0;
    border-radius: 3px;
    cursor: pointer;
    transition: .3s ease-in-out;
  }

  ::-webkit-calendar-picker-indicator:hover{
    background-color: white;
    border-radius: 3px;
    cursor: pointer;
    transition: .3s ease-in-out;
  }
  
`

const RateBar = styled.div`
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  color: #BECCE0;
  padding: 8px 10px;
`

const ButtonBar = styled.div`
  width: 416px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;

  button {
    background-color: #1B2F4A;
    width: 100%;
    height: inherit;
    border: none;
    border-bottom: solid 2px #BECCE0;
    cursor: pointer;
    color: #BECCE0;
    //border: 2px solid #D3D3D3;
    transition: .3s ease-in-out;
  }

  button:hover{
    background-color: #4377EA;
    border-bottom: solid 2px white;
    color: white;
    transition: .3s ease-in-out;
  }

`






